import React from 'react'
import { dashboard_link_tree } from '../../misc/data'
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Button } from '@mui/material';
export default function DashboadNav() {
  return (
    <div> <div className="dashboard_link_tree">

    {dashboard_link_tree.map(links => {
      return (
        <Accordion
          defaultExpanded={true}
          sx={{
            backgroundColor: "transparent",

          }}
          elevation={0}
          // expanded={expanded === links.value} onChange={handleChange(links.value)}
          >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls={links.value}
            id={links.value}

          >
            <Typography sx={{
              fontWeight: "bold"
            }} >
              {links.label}
            </Typography>

          </AccordionSummary>
          <AccordionDetails>
            <div>

              {links.childrens.map(ch => <Button
                fullWidth
                sx={{
                  color: "#252733",
                  textAlign: "left",
                  justifyContent: "start"
                }}
                key={ch.value}>{ch.label}</Button>)}
            </div>
          </AccordionDetails>
        </Accordion>
      )
    })}
  </div></div>
  )
}
