import * as React from "react";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import { Button, FormGroup, TextField } from "@mui/material";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Link, useLocation } from "react-router-dom";

export function GenericTables({ rows, title, cellTitles, type, seachOption, titleOption }) {
  const location = useLocation();
  return (
    <div className="GenericTables">
      {(titleOption || seachOption) &&
        <div className="table-title-search">
          {titleOption && <h4 className="title">
            {title}
          </h4>}
          {seachOption && <div className="search">
            <SearchOption />
          </div>}

        </div>}

      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              {cellTitles.map((ct, i) => (
                <StyledTableCell
                  align={i === 0 ? "left" : "right"}
                  className="text-start"
                >
                  {ct}
                </StyledTableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <StyledTableRow key={row.title}>
                <StyledTableCell
                  style={{ width: "40%" }}
                  component="th"
                  scope="row"
                >
                  {row.title}
                </StyledTableCell>
                <StyledTableCell style={{ width: "12%" }} align="left">
                  {row.author}
                </StyledTableCell>
                <StyledTableCell style={{ width: "12%" }} align="left">
                  {row.category}
                </StyledTableCell>
                <StyledTableCell style={{ width: "12%" }} align="left">
                  {row.tag}
                </StyledTableCell>
                <StyledTableCell style={{ width: "12%" }} align="left">
                  {row.publishDate}
                </StyledTableCell>
                <StyledTableCell style={{ width: "12%" }} align="left">
                  <Link to="/reports/report-details">
                    <Button
                      variant="contained"
                      disableElevation
                      size="large"
                    // href={row.url}
                    >
                      <span style={{
                        whiteSpace: "nowrap",
                        display: "flex",
                        alignItems: "center"
                      }}>

                        {type === "edit" ? "Edit" : "Read More"}
                        {type === "edit" && <svg className="ml-xs-8" width="18" height="18" viewBox="0 0 18 18" fill="none" xmlns="http://www.w3.org/2000/svg">
                          <path d="M8.15659 2.68683H2.59035C2.16857 2.68683 1.76405 2.85438 1.4658 3.15263C1.16755 3.45088 1 3.85539 1 4.27718V15.4097C1 15.8314 1.16755 16.236 1.4658 16.5342C1.76405 16.8324 2.16857 17 2.59035 17H13.7228C14.1446 17 14.5491 16.8324 14.8474 16.5342C15.1456 16.236 15.3132 15.8314 15.3132 15.4097V9.84342" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                          <path d="M14.1204 1.49406C14.4368 1.17772 14.8658 1 15.3132 1C15.7605 1 16.1896 1.17772 16.5059 1.49406C16.8223 1.8104 17 2.23945 17 2.68682C17 3.1342 16.8223 3.56325 16.5059 3.87959L8.95176 11.4338L5.77106 12.2289L6.56623 9.04824L14.1204 1.49406Z" stroke="white" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round" />
                        </svg>

                        }

                      </span>
                    </Button>
                  </Link>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
}


export function SearchOption() {
  return (
    <div className="SearchOption">
      <FormGroup row>
        <TextField
          label="Search by keyword"
          type="search"
          variant="outlined"
          placeholder="Search by keyword"
        />
        {/* <TextField variant="outlined" placeholder="Search by keyword" /> */}
        <Button variant="contained" disableElevation>
          Search
        </Button>
      </FormGroup>
    </div>
  );
}






export function RadioInputs({ value, handleChange, list }) {
  return (
    <div>
      <FormControl>
        {/* <FormLabel id="demo-controlled-radio-buttons-group">Gender</FormLabel> */}
        <RadioGroup
          aria-labelledby="demo-controlled-radio-buttons-group"
          name="controlled-radio-buttons-group"
          value={value}
          onChange={handleChange}
        >
          {list.map((v) => (
            <FormControlLabel
              value={v.value}
              key={v.value}
              control={<Radio />}
              label={v.label}
            />
          ))}
        </RadioGroup>
      </FormControl>
    </div>
  );
}

const TagButton = styled(Button)(({ theme }) => ({
  color: "rgba(0, 0, 0, 0.87)",
  backgroundColor: "#EDEDF0",
  borderRadius: 22,
  marginRight: 12,
  marginBottom: 12,
  boxShadow: "none",
  "&:hover": {
    color: "#fff",
    backgroundColor: "#0B4DA2;",
  },
  "&.selected": {
    color: "#fff",
    backgroundColor: "#0B4DA2;",
  },
}));

export function Tags({ value, handleChange, list }) {
  return (
    <div className="Tags">
      {list.map((v) => (
        <TagButton
          key={v.value}
          variant="contained"
          className={value === v ? "selected" : ""}
          onClick={(e) => handleChange(v)}
        >
          {v.label}
        </TagButton>
      ))}
    </div>
  );
}

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: "#74AAF1",
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  // hide last border
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

